#loading-screen {
  z-index: 3;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  background-color: #000;
  transition: opacity 1.5s;
  position: fixed;
  top: 0;
  left: 0;
}

#loader {
  height: 100vh;
  width: 100%;
  display: grid;
}

.coloredLogoLoadingScreenQD {
  z-index: 1;
  width: 25%;
  max-width: 100vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#dApp-loadingScreen {
  width: 10%;
  height: 7%;
  color: #0000;
  cursor: pointer;
  background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927) 0 0 / 500%;
  -webkit-background-clip: text;
  background-clip: text;
  border: 1.5px solid #fff3;
  transition: all 1s;
  animation: 3.5s infinite alternate bg-animation;
  display: none;
  position: absolute;
  top: 79%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#dApp-loadingScreen:hover {
  border-color: #fff;
  box-shadow: 5px 5px #fff;
}

#loading-screen.fade-out {
  opacity: 0;
}

#loadind-sceen.fixed {
  z-index: 999;
  position: fixed;
}

#loading-screen.fade-in {
  opacity: 1;
}

li, a, button {
  cursor: pointer;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 17px;
  font-weight: 500;
  text-decoration: none;
}

header {
  z-index: 5;
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
  background-color: #030303e6;
  border-bottom: 1.5px solid #fff;
  justify-content: space-between;
  align-items: center;
  padding: 16px 10%;
  transition: height 1.5s;
  display: flex;
  position: fixed;
  transform: translateY(-100%);
}

.logoHeaderQD {
  cursor: pointer;
  width: 7%;
  transition: all 1s;
}

.logoHeaderQD:hover {
  filter: drop-shadow(0 0 3px #f9d44e);
}

.nav-links {
  padding: 0;
  list-style: none;
}

.nav-links li {
  cursor: pointer;
  text-transform: none;
  padding: 0 20px;
  transition: all 2s;
  display: inline-block;
  position: relative;
}

.nav-links li:hover {
  color: #0000;
  background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927) 0 0 / 500%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: 3.5s infinite alternate bg-animation;
}

.nav-links li:after {
  content: "";
  width: 0%;
  height: 1px;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  bottom: -1px;
  left: 75%;
}

.nav-links li:hover:after {
  width: 50%;
  left: 25%;
}

.dApp-button {
  cursor: pointer;
  background: none;
  border: 1.5px solid #0000;
  padding: 5px 10px;
  transition: all 1s;
}

.dApp-button:hover {
  color: #0000;
  background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927) 0 0 / 500%;
  -webkit-background-clip: text;
  background-clip: text;
  border-color: #fff;
  animation: 3.5s infinite alternate bg-animation;
  box-shadow: 5px 5px #fff;
}

.hoverGradient {
  color: #0000;
  background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927) 0 0 / 500%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: 3.5s infinite alternate bg-animation;
}

.dApp-buttonHover {
  color: #0000;
  background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927) 0 0 / 500%;
  -webkit-background-clip: text;
  background-clip: text;
  border-color: #fff;
  animation: 3.5s infinite alternate bg-animation;
  box-shadow: 5px 5px #fff;
}

.display {
  animation: .5s forwards header-animation-in;
}

.hide {
  animation: .5s forwards header-animation-out;
}

@keyframes bg-animation {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 100%;
  }
}

@keyframes header-animation-in {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes header-animation-out {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(-100%);
  }
}

@media screen and (max-width: 730px) {
  li, a, button {
    font-size: 35px;
    font-weight: 500;
  }

  header {
    grid-template-rows: 15% 60% 25%;
    justify-content: center;
    place-items: center;
    display: grid;
  }

  .openHeaderFullScreen {
    height: 95.6%;
  }

  .logoHeaderQD {
    cursor: pointer;
    width: 15%;
    grid-row: 1 / 4;
    align-self: center;
    transition: all 1s;
  }

  .logoHeaderPosition {
    grid-row: 1 / 2;
  }

  .nav-section {
    height: 100%;
    width: 100%;
    grid-row: 2 / 3;
    display: none;
  }

  .nav-section ul {
    text-align: center;
    height: 100%;
    width: 100%;
    grid-template-rows: repeat(4, 1fr);
    align-items: center;
    margin: 0;
    list-style-position: inside;
    display: grid;
  }

  .nav-links li:after {
    content: "";
    width: 0%;
    height: 1px;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    bottom: -1px;
    left: 60%;
  }

  .nav-links li:hover:after {
    width: 20%;
    left: 40%;
  }

  .button-section {
    grid-row: 3 / 4;
    display: none;
  }

  .displayContent {
    display: grid;
  }
}

