/* Loading screen - perfect */
#loading-screen {
	position: fixed;
	z-index: 3;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: #000000;
	opacity: 1;
 	transition: 1.5s opacity;
}
#loader {
    display: grid;
    height: 100vh;
    width: 100%;
}
/* QD logo setup - perfect */
.coloredLogoLoadingScreenQD {
    z-index: 1;
    position: absolute;
    left: 50%;
    top: 50%;

    width: 25%;
    max-width: 100vw;

    transform: translate(-50%, -50%);
}
#dApp-loadingScreen {
    position: absolute;
    left: 50%;
    top: 79%;

    width: 10%;
    height: 7%;

    transform: translate(-50%, -50%);

    background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927);
    background-clip: text;
    color: transparent;
    background-size: 500%;
    animation: bg-animation 3.5s infinite alternate;

    cursor: pointer;

    border: none;
    transition: 1s;
    border: 1.5px solid rgba(255, 255, 255, .2);

    display: none;
}
#dApp-loadingScreen:hover {
    border-color:white;
    box-shadow: 5px 5px 0px 0px white;
}
#loading-screen.fade-out {
    opacity: 0;
}
#loadind-sceen.fixed {
    position: fixed;
    z-index: 999;
}
#loading-screen.fade-in {
    opacity: 1;
}

/* Navigation header - perfect */
li, a, button {
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 17px;
    color: white;
    text-decoration: none;
}
header {
     z-index: 5;
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding: 16px 10%;
     border-bottom: 1.5px solid white;
     background-color: rgba(3, 3, 3, 0.9);
     backdrop-filter: blur(6px);
     position: fixed;
     transition: 1.5s;
     transition: height 1.5s;
     transform: translateY(-100%);
}
.logoHeaderQD {
    cursor: pointer;
    width: 7%;
    transition: 1s;
}
.logoHeaderQD:hover {
    filter: drop-shadow(0px 0px 3px #F9D44E);
}
.nav-links {
    list-style: none;
    padding: 0;
}
.nav-links li {
    cursor: pointer;
    display: inline-block;
    padding: 0px 20px;
    transition: 2s;
    text-transform: none;
    position: relative;
}
.nav-links li:hover {
    background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927);
    background-clip: text;
    color: transparent;
    background-size: 500%;
    animation: bg-animation 3.5s infinite alternate;
}
.nav-links li:after {
    content: "";
    position: absolute;
    width: 0%;
    bottom: -1px;
    left: 75%;
    background-color: white;
    height: 1px;
    transition: 0.3s;
}
.nav-links li:hover:after {
    width: 50%;
    left: 25%;
}
.dApp-button {
    background: transparent;
    border: none;
    cursor: pointer;
    transition: 1s;
    padding: 5px 10px;
    border: 1.5px solid rgba(0, 0, 0, 0);
}
.dApp-button:hover {
    border-color:white;
    box-shadow: 5px 5px 0px 0px white;
    background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927);
    background-clip: text;
    color: transparent;
    background-size: 500%;
    animation: bg-animation 3.5s infinite alternate;
}
.hoverGradient {
    background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927);
    background-clip: text;
    color: transparent;
    background-size: 500%;
    animation: bg-animation 3.5s infinite alternate;
}
.dApp-buttonHover {
    border-color:white;
    box-shadow: 5px 5px 0px 0px white;
    background: linear-gradient(to right, #40e5a0, #19664f, #3cceff, #153969, #db295c, #610927);
    background-clip: text;
    color: transparent;
    background-size: 500%;
    animation: bg-animation 3.5s infinite alternate;
}
.display {
    animation: header-animation-in 0.5s forwards;
}
.hide {
    animation: header-animation-out 0.5s forwards;
}
@keyframes bg-animation {
    0% {
        background-position: left;
    }
    100% {
        background-position: right;
    }
}
@keyframes header-animation-in {
    0% {
        transform: translateY(-100%);
    }
    100% {
        transform: translateY(0%);
    }
}
@keyframes header-animation-out {
    0% {
        transform: translateY(0%);
    }
    100% {
        transform: translateY(-100%);
    }
}
/* Header phone from 730px and lower - perfect */
@media screen and (max-width: 730px) {
    li, a, button {
        font-weight: 500;
        font-size: 35px;
    }
    header {
        display: grid;
        grid-template-rows: 15% 60% 25%;
        justify-content: center;
        align-items: center;
        justify-items: center;
    }
    .openHeaderFullScreen {
        height: 95.6%;
    }
    .logoHeaderQD {
        cursor: pointer;
        width: 15%;
        transition: 1s;
        align-self: center;
        grid-row: 1/4;
    }
    .logoHeaderPosition {
        grid-row: 1/2;
    }
    .nav-section {
        display: none;
        height: 100%;
        width: 100%;
        grid-row: 2/3;
    }
    .nav-section ul {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        align-items: center;
        text-align: center;
        list-style-position: inside;
        height: 100%;
        width: 100%;
        margin: 0;
    }
    .nav-links li:after {
        content: "";
        position: absolute;
        width: 0%;
        bottom: -1px;
        left: 60%;
        background-color: white;
        height: 1px;
        transition: 0.3s;
    }
    .nav-links li:hover:after {
        width: 20%;
        left: 40%;
    }
    .button-section {
        display: none;
        grid-row: 3/4;
    }
    .displayContent {
        display: grid;
    }
}

