/* Constant values */
:root {
    font-family: Arial, Helvetica, sans-serif;
}

/* Threejs canvas full screen */
canvas {
    z-index: 3;
    position: fixed;
    top: 0;
    left: 0;
    background: none;
}

/* Website */
body {
    margin: 0;
    background-color: #090631;
}
body::-webkit-scrollbar { 
    display: none;
}
main {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-columns: repeat(20, 5%);
}

/* Cursor */
.cursor {
    z-index: 3;
    position: absolute;
    pointer-events: none;
    border-radius: 50%;
    top: 0.05%;
    left: 0.55%;
    transition: transform .5s;
    backdrop-filter: opacity(0%);

    background: none;
    /* border: 1px solid white; */
    width: 50px;
    height: 50px;
}

/* Background gradient */
